import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';

export default function header(props) {
  const { pathname } = props.location;
  const role = localStorage.getItem('extuuii');
  const name = localStorage.getItem('name');
  const abbr = (name != null) ? name.charAt(0) : 'X';
  const Menu = (props) => {
    const menus = (typeof props.data !== 'undefined') ? props.data : [];
    return (
      <div className="menu-section">
        {menus.map((val, idx) => (
          pathname.includes(val.url) ?
            <div key={idx} className={`menu-item active`}><a><i className={`icon fas fa-${val.icon}`} /> {val.menu}</a></div>
            :
            <div key={idx} className={`menu-item`}><Link to={val.url}><i className={`icon fas fa-${val.icon}`} /> {val.menu}</Link></div>

        ))}
      </div>
    )
  };

  return (
    <>
      <nav className="navbar navbar-expand-lg navbar-custom">
        <div className=" collapse navbar-collapse " id="navbarNav">
          <ul className="navbar-nav">
            <li className="nav-item">
              <div className="profile-section">
                <h6 className="usr">PT SAKURA JAYA</h6>
                <div><span className="sub"> Premium user</span></div>
              </div>
            </li>
          </ul>
        </div>
        <div className="row">
          <div className="col-md-12">
            <Link to="/logout" className="btn btn-md btn-outline-gray"> <i className="fal fa-sign-out"></i> Logout</Link>
          </div>
        </div>
      </nav>
      <div className="sidebar">
        <div className="sidebar-brand">
          <img height="30px" src="/assets/images/icon.png" />
        </div>
        <Menu active="List Article" data={
          [
            { menu: 'Dashboard', url: '/dashboard', icon: 'th-large' },
            { menu: 'List Employee', url: '/employees', icon: 'list' },
            { menu: 'Configuration', url: '/configuration', icon: 'cogs' },
          ]
        } />
      </div>
    </>
  );
} 